import { a as getCategoryNameById, g as getCategoryDescription } from "../../../../../chunks/categories.js";
import { s as supabase } from "../../../../../chunks/supabaseClient.js";
const prerender = "false";
async function load({ params }) {
  const catid = params.catid;
  console.log("Lade Cat Parameter ...", catid);
  const category = await getCategoryNameById(parseInt(catid));
  const description = await getCategoryDescription(parseInt(catid));
  console.log("desc", description);
  const { data: products, error } = await supabase.from("productwithpricecategories").select().eq("category_id", catid).order("name");
  const length = products?.length;
  return {
    catid,
    length,
    category,
    description,
    products
  };
}
export {
  load,
  prerender
};
